.footer {
  background-color: $white;
  padding: 30px 0;

  &__grid {
    display: grid;
    grid-template-columns: 400px 1fr;
  }

  &__col {
    &:first-child {
      border-right: 1px solid #707070;
      padding-right: 30px;
    }

    &:last-child {
      padding-left: 30px;
    }
  }

  &__logo {
    display: inline-flex;

    img {
      height: 50px;
    }
  }

  &__content {
    margin-top: 20px;

    a {
      color: inherit;
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 10px;

    &:not(:first-child) {
      margin-top: 24px;
    }
  }

  @media (max-width: 1199px) {
    &__grid {
      grid-template-columns: 1fr;
    }

    &__col {
      &:first-child {
        border-right: 0;
        padding-right: 0;
        margin-bottom: 30px;
        padding-bottom: 30px;
        border-bottom: 1px solid #777;
      }

      &:last-child {
        padding-left: 0;
      }
    }
  }
}

.f-social {
  display: flex;

  &__item {
    display: flex;
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    color: $white;
    background-color: #555656;
    border-radius: 50%;

    &:hover {
      color: $white;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 15px;
    }

    .fa {
      display: flex;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: center;
      transition: 0.3s;
      border-radius: 50%;
    }

    &:hover .fa-facebook {
      background-color: $facebook;
    }

    &:hover .fa-youtube-play {
      background-color: $youtube;
    }

    &:hover .fa-pinterest {
      background-color: $pinterest;
    }

    &:hover .fa-instagram {
      background-color: $instagram;
    }

    &:hover .fa-tumblr {
      background-color: $tumblr;
    }

    &:hover .fa-twitter {
      background-color: $twitter;
    }
  }

  @media (max-width: 991px) {
    &__item {
      &:not(:last-child) {
        margin-right: 8px;
      }
    }
  }
}

.f-menu {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__link {
    display: inline-block;
    padding: 0 24px;
    margin: 0 -24px;
    position: relative;
    left: 0;
    transition: 0.3s;
    color: #333;
    line-height: 20px;

    &.active,
    &:hover {
      color: $primary;
      text-decoration: none;
      left: 24px;
    }

    &::before {
      content: "";
      display: block;
      width: 14px;
      height: 2px;
      background-color: currentColor;
      position: absolute;
      top: 9px;
      left: -16px;
      border-radius: 2px;
      transition: 0s;
      opacity: 0;
    }

    &::after {
      content: "";
      display: block;
      width: 8px;
      height: 8px;
      border-top: 2px solid currentColor;
      border-right: 2px solid currentColor;
      position: absolute;
      top: 10px;
      left: 0;
      transition: 0s;
      opacity: 0;
      transform: translateY(-50%) rotate(45deg);
    }

    &:hover::before {
      opacity: 1;
      left: 0px;
      transition: 0.3s 0.1s;
    }

    &:hover::after {
      opacity: 1;
      left: 8px;
      transition: 0.3s 0.2s;
    }

    &.active::before {
      opacity: 1;
      left: 0px;
    }

    &.active::after {
      opacity: 1;
      left: 8px;
    }
  }
}

.f-download {
  display: flex;

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}
