.follow {
  background-color: $primary;
  padding: 18px 0;

  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__label {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: $white;
    text-transform: uppercase;
    margin-right: 26px;

    img {
      height: 32px;
      margin-right: 24px;
    }
  }

  &__form {
    flex-grow: 1;
    max-width: 635px;
    border: 3px solid $white;
    border-radius: 4px;
    background-color: $white;

    .form-control {
      padding-left: 12px;
      border: 0;
      height: 38px;
    }

    .input-group-append {
      background: 0;
    }

    .input-group-text {
      font-size: 14px;
      font-weight: 700;
      color: $white;
      background: #3f9dd6;
      padding: 0 30px;
      border: 0;
      border-radius: 4px !important;
    }
  }

  @media (max-width: 767px) {
    &__inner {
      flex-direction: column;
      align-items: center;
    }

    &__label {
      margin-right: 0;
      margin-bottom: 18px;

      img {
        height: 24px;
        margin-right: 20px;
      }
    }

    &__form {
      width: 100%;
    }
  }
}

.fs-title {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  color: $white;

  img {
    height: 25px;
    width: 25px;
    object-fit: cover;
    object-position: 50% 50%;
    border-radius: 50%;
    margin-right: 10px;
  }
}

.countdown {
  display: flex;
  align-items: center;
  margin-left: 10px;
  padding-left: 10px;
  border-left: 2px solid $white;
  color: $white;

  &__timer {
    display: flex;

    span {
      display: inline-flex;
      min-width: 30px;
      height: 24px;
      font-weight: 700;
      background-color: $white;
      border-radius: 4px;
      justify-content: center;
      align-items: center;
      color: #333;
      font-size: 14px;
      margin-left: 10px;
      padding: 0 6px;
      text-align: center;

      & + span {
        margin-left: 15px;
        position: relative;

        &::before {
          content: ":";
          display: flex;
          width: 15px;
          height: 100%;
          justify-content: center;
          align-items: center;
          color: $white;
          position: absolute;
          top: 50%;
          left: -15px;
          transform: translateY(-50%);
        }
      }
    }
  }

  @media (max-width: 575px) {
    flex: 0 0 100%;
    margin-left: 0;
    padding-left: 0;
    border-left: 0;
    margin-top: 8px;
    justify-content: flex-end;
  }
}

.flash-sale-tabs {
  background-color: $white;
  padding: 8px 20px;
}

.flash-timer {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  background-color: $white;
  padding: 10px;
  margin-bottom: 20px;

  &__item {
    display: block;
    text-align: center;
    color: #333;

    &:hover {
      color: #333;
      text-decoration: none;
    }

    &.active {
      color: $danger;

      &:hover {
        color: $danger;
      }
    }
  }

  &__time {
    font-size: 20px;
    font-weight: 700;
  }

  @media (max-width: 575px) {
    &__time {
      font-size: 16px;
    }

    &__label {
      font-size: 12px;
      line-height: 1.2;
    }
  }
}

.link {
  color: $secondary;
  text-decoration: underline;

  &:hover {
    color: $secondary;
  }
}

.social-btn {
  display: flex;
  height: 35px;
  align-items: center;
  background-color: $facebook;
  color: $white;
  font-size: 14px;
  position: relative;
  z-index: 1;
  border-radius: 4px;

  &:hover {
    color: $white;
    text-decoration: none;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    position: absolute;
    top: 0;
    left: 0;
  }

  &:hover::before {
    background-color: rgba(#fff, 0.2);
  }

  &__icon {
    display: flex;
    width: 38px;
    height: 35px;
    justify-content: center;
    align-items: center;
    border-right: 1px solid rgba(#000, 0.2);
    justify-content: center;
    align-items: center;
    padding: 8px;
  }

  &__text {
    flex-grow: 1;
    padding: 4px 8px;
    text-align: center;
  }

  &--google {
    background-color: $google-plus;
  }

  &--zalo {
    background-color: $zalo;
  }
}

.float-sidebar {
  display: flex;
  width: 300px;
  height: 100vh;
  flex-direction: column;
  position: fixed;
  z-index: 200;
  top: 0;
  left: -100%;
  background-color: $white;
  transition: 0.3s;

  &::before {
    content: "";
    display: block;
    width: 200vw;
    height: 100vh;
    background-color: #000;
    opacity: 0;
    position: fixed;
    z-index: -2;
    top: 0;
    left: 0;
    transition: left 0s 0.3s, opacity 0.3s;
    transform: translateX(-100%);
  }

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: $white;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 4px;
    border-bottom: 1px solid $border-color;
  }

  &__close {
    display: flex;
    align-items: center;
  }

  &__body {
    padding: 10px 15px;
    flex-grow: 1;
    overflow-x: hidden;
    overflow-y: auto;
  }

  &.is-show {
    left: 0;
  }

  &.is-show::before {
    transform: translateX(0);
    opacity: 0.5;
    transition: transform 0s, opacity 0.3s;
  }

  @media (min-width: 992px) {
    display: block;
    width: auto;
    height: auto;
    position: static;
    top: auto;
    left: auto;
    background: 0;

    &::before,
    &::after {
      display: none;
    }

    &__header {
      display: none;
    }

    &__body {
      padding: 0;
      overflow: unset;
    }
  }
}

.product-filter-btn {
  position: fixed;
  z-index: 50;
  bottom: 10px;
  left: 15px;
  background: $primary;
  line-height: 35px;
  border: 0;
  padding: 0 20px;
  border-radius: 18px;
  color: $white;
  @include shadow("");

  &:hover {
    color: $white;
    text-decoration: none;
  }
}

.support-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: $white;
  box-shadow: 0 1px 10px rgba(#000, 0.16);

  &__body {
    padding: 30px 50px;
  }

  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 1.2;
    text-transform: uppercase;
    margin-bottom: 16px;
  }

  &__banner {
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  .form-control {
    padding: 10px 20px;
    border-radius: 0;
  }

  input.form-control {
    height: 44px;
  }

  @media (max-width: 1199px) {
    &__body {
      padding: 30px 40px;
    }

    &__title {
      font-size: 20px;
    }
  }

  @media (max-width: 991px) {
    display: block;

    &__banner {
      display: none;
    }

    .button {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  @media (max-width: 767px) {
    &__body {
      padding: 20px;
    }

    &__title {
      font-size: 18px;
    }
  }
}
