.banner-slider {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;

  &__container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__frame {
    @include frame("rect", 45%);
    height: 100%;
  }

  &__prev,
  &__next {
    display: flex;
    width: 10%;
    height: 100%;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    cursor: pointer;
    font-size: 20px;
    color: $white;
    background: linear-gradient(to right, rgba(#000, 0.4), transparent);
    opacity: 0;
    transition: 0.4s;

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: 0;
    background: linear-gradient(to left, rgba(#000, 0.4), transparent);
  }

  &:hover &__prev,
  &:hover &__next {
    opacity: 1;
  }

  &__pagination {
    display: none;
  }

  .swiper-slide {
    height: auto;
  }

  @media (max-width: 1199px) {
    &__container {
      position: static;
    }

    &__prev,
    &__next {
      opacity: 1;
    }
  }
}

.cat-slider {
  padding: 15px;
  position: relative;
  z-index: 1;
  @include shadow("");

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46px;
    height: 46px;
    border-radius: 50%;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }

  &__text {
    font-size: 13px;
    margin-top: 10px;
  }

  &__prev,
  &__next {
    display: flex;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: rgba(#fff, 0.8);
    @include shadow("");
    cursor: pointer;
    font-size: 16px;
    color: #000;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);

    &:focus {
      outline: 0;
    }

    &.swiper-button-disabled {
      display: none;
    }
  }

  &__next {
    left: 100%;
  }

  @media (max-width: 575px) {
    padding: 8px;

    &__text {
      font-size: 11px;
      white-space: nowrap;
      margin-top: 6px;
    }
  }
}

.product-slider {
  position: relative;
  z-index: 1;

  &__prev,
  &__next {
    display: flex;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: rgba(#fff, 0.8);
    @include shadow("");
    cursor: pointer;
    font-size: 16px;
    color: #000;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);

    &:focus {
      outline: 0;
    }

    &.swiper-button-disabled {
      display: none;
    }
  }

  &__next {
    left: 100%;
  }
}

.calendar-slider {
  padding: 18px 0;
  position: relative;
  z-index: 1;

  &__pagination {
    display: flex;
    width: 100%;
    bottom: 3px;
    border-radius: 2px;
    background-color: rgba(#fff, 0.4);
    position: absolute;
    bottom: 0;
    left: 0;

    .swiper-pagination-bullet {
      display: flex;
      opacity: 1;
      border-radius: 3px;
      height: 3px;
      flex: 1;
      background: 0;
      position: relative;

      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-color: $white;
        transition: 0.3s linear;
      }

      &:first-child::before {
        border-radius: 3px 0 0 3px;
      }

      &:last-child::before {
        border-radius: 0 3px 3px 0;
      }

      &-active ~ .swiper-pagination-bullet::before {
        width: 0;
      }
    }
  }
}

.rank-slider {
  width: 100%;
  height: 560px;
  position: relative;
  z-index: 1;

  &__container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__slide {
    cursor: pointer;
    position: relative;
    padding-right: 30px;

    &.is-selected {
      box-shadow: inset -3px 0 0 $secondary;
    }
  }
}

.expert-slider {
  position: relative;
  z-index: 1;
  margin: -8px;

  .swiper-slide {
    padding: 8px;
  }

  &__prev,
  &__next {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: $secondary;
    color: $white;
    position: absolute;
    top: 50%;
    left: 8px;
    transform: translate(-50%, -50%);
    z-index: 10;
  }

  &__next {
    left: auto;
    right: 8px;
    transform: translate(50%, -50%);
  }
}
