.section {
  margin-bottom: 20px;

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    background-color: $primary;
    padding: 13px 30px;
    margin-bottom: 20px;
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    margin-bottom: 0;
  }

  &__link {
    font-size: 16px;
    font-weight: 700;
    color: $white;

    &:hover {
      color: $white;
      text-decoration: none;
    }
  }

  &__body {
    background-color: $white;
    margin-top: -20px;
    padding: 10px;
  }

  @media (max-width: 991px) {
    &__header {
      padding: 8px 16px;
    }

    &__title {
      font-size: 14px;
      line-height: 20px;
    }

    &__link {
      font-size: 14px;
      line-height: 20px;
    }
  }
}

.section-2 {
  margin-bottom: 20px;

  &__header {
    @include shadow("");
    background-color: $white;
    padding: 15px 20px;
    margin-bottom: 10px;
  }

  &__title {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 700;
    color: $secondary;
    margin-bottom: 10px;

    img {
      width: 24px;
      height: 24px;
      object-fit: contain;
      object-position: 50% 50%;
      margin-right: 16px;
    }
  }

  @media (max-width: 991px) {
    &__header {
      padding: 8px 16px;
    }

    &__title {
      font-size: 14px;
      line-height: 24px;

      img {
        margin-right: 12px;
      }
    }
  }
}

.section-3 {
  @include shadow("");
  background-color: $white;
  padding: 20px;

  &__title {
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    border-bottom: 1px solid $border-color;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }
}

.section-4 {
  background-color: $white;

  & + & {
    margin-top: 20px;
  }

  &__header {
    border-bottom: 1px solid $border-color;
    padding: 15px 20px;
  }

  &__title {
    font-size: 18px;
    margin-bottom: 0;
    line-height: 30px;
  }

  &__subtitle {
    color: #666;
  }

  &__body {
    padding: 20px;
  }

  @media (max-width: 767px) {
    &__header {
      padding: 10px 15px;
    }

    &__title {
      font-size: 16px;
      line-height: 24px;
    }

    &__body {
      padding: 15px;
    }
  }
}

.aside {
  padding: 20px 10px;
  background-color: $white;
  @include shadow("");

  &__title {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 15px;

    &:not(:first-child) {
      margin-top: 20px;
      padding-top: 15px;
      border-top: 1px solid $border-color;
    }
  }
}

.section-5 {
  padding: 60px 0;

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 20px;
    gap: 6px;
    justify-content: space-between;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
    margin-bottom: 20px;
  }

  &__more {
    color: inherit;

    i {
      transition: 0.3s;
    }

    &:hover {
      color: $primary;
      text-decoration: none;

      i {
        transform: translateX(3px);
      }
    }
  }

  &__header &__title {
    margin-bottom: 0;
  }
}

.section-6 {
  padding: 60px 0;

  &__title {
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 1em;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 20px;
    }
  }
}
