.menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &-item {
    margin: 4px 0 8px;
    position: relative;
  }

  &-link {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    padding: 0 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: inherit;

    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }

  &-toggle {
    display: inline-flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    span {
      display: inline-flex;
      width: 24px;
      height: 24px;
      justify-content: center;
      align-items: center;
      border-radius: 12px;
      transition: 0.3s;

      &:hover {
        background-color: rgba(#000, 0.1);
      }
    }

    &.active span {
      transform: rotate(90deg);
    }
  }

  &-sub {
    display: none;
  }

  &-sub &-link {
    padding-left: 40px;
  }

  &-sub-2 &-link {
    padding-left: 60px;
  }

  &-sub-3 &-link {
    padding-left: 80px;
  }

  &-item:hover > &-link {
    background-color: rgba($primary, 0.05);
  }

  &-root > &-item > &-link {
    font-weight: 700;
  }

  @media (min-width: #{$navbar-breakpoint}px) {
    &-item {
      margin: 0;
    }

    &-toggle {
      display: none;
    }

    &-sub {
      @include shadow("xs");
      width: 100%;
      background-color: $white;
      position: absolute;
      z-index: 20;
      top: 0;
      left: 100%;
      transform-origin: 0 0;
    }

    &-sub &-link {
      padding-left: 20px !important;
    }

    &-item:hover > &-sub {
      display: block;
      animation: menu-float-show 0.2s ease-in-out;
    }

    &-root {
      position: relative;
    }

    &-root > &-item {
      position: static;
    }

    &-root > &-item > &-link {
      position: relative;

      &::after {
        content: "\f105";
        font-family: fontawesome;
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
      }
    }

    &-mega {
      display: none;
      grid-gap: 20px;
      padding: 20px;
      width: calc(300% + 10px);
      min-height: 100%;
      background-color: $white;
      box-shadow: 1px 2px 4px rgba(#000, 0.3);
      position: absolute;
      z-index: 110;
      top: 0;
      left: 100%;
    }

    &-root > &-item:hover > &-mega {
      display: block;
    }

    &-mega &-sub {
      display: block;
      width: 100%;
      position: static;
      box-shadow: unset;
      border-radius: 0;
    }

    &-mega &-link {
      padding: 0;
      font-size: 12px;
      line-height: 24px;

      &:hover {
        color: $secondary;
      }
    }

    &-mega &-item:hover > &-link {
      padding: 0;
      background: 0;
    }

    &-mega &-item:hover > &-sub {
      animation: none;
    }

    &-mega > &-sub {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 20px;
    }

    &-mega > &-sub > &-item > &-link {
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 700;
      margin-bottom: 6px;
    }
  }
}

.n-menu {
  display: none;

  &__title {
    display: none;
    font-size: 14px;
    font-weight: 700;
    line-height: 22px;
    padding: 10px;
    text-align: center;
    color: $white;
    background-color: $primary;
    margin-bottom: 0;
    cursor: pointer;
  }

  @media (min-width: 1200px) {
    position: relative;
    z-index: 50;

    &__title {
      display: block;
    }

    &__dropdown {
      display: none;
      background-color: $white;
      width: 100%;
      position: absolute;
      top: 100%;
      left: 0;
      @include shadow("");
      transform-origin: 0 0;
    }

    &:hover &__dropdown {
      display: block;
      animation: menu-float-show 0.3s ease-in-out;
    }
  }
}

.as-menu {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: $white;

  &__title {
    font-size: 14px;
    font-weight: 700;
    line-height: 22px;
    padding: 10px;
    text-align: center;
    color: $white;
    background-color: $primary;
    margin-bottom: 0;
  }

  &__body {
    flex-grow: 1;
  }

  @media (min-width: 1200px) {
    .menu-mega {
      top: -42px;
      min-height: calc(100% + 42px);
    }
  }
}

.pd-menu {
  margin-top: -5px;
  overflow: hidden;
  height: 420px;
  position: relative;

  &__list {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
  }

  &__list &__list {
    padding-left: 10px;
  }

  &__link {
    display: inline-block;
    color: #333;
    padding: 5px 0;

    &:hover {
      color: $primary;
      font-weight: 700;
      text-decoration: none;
    }
  }

  &__toggle {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 30px 0 5px;
    text-align: center;
    color: $secondary;
    font-weight: 700;
    position: absolute;
    bottom: 0;
    z-index: 10;
    background: linear-gradient(to bottom, transparent, $white, $white);

    &:hover {
      color: $secondary;
      text-decoration: none;
    }

    &::before {
      content: attr(data-expand);
    }
  }

  &.show {
    height: auto;
  }

  &.show &__toggle {
    position: static;
    padding: 10px 0;

    &::before {
      content: attr(data-collapse);
    }

    .fa {
      transform: rotateX(180deg);
    }
  }
}

.h-menu {
  border-top: 1px solid $border-color;
  padding-top: 10px;

  .menu-link {
    img {
      width: 24px;
      height: 24px;
      object-fit: contain;
      object-position: 50% 50%;
      margin-right: 10px;
    }
  }

  @media (min-width: 1200px) {
    display: flex;
    padding: 0 0 0 12px;
    margin: 0;
    border: 0;

    .menu-item {
      margin: 0;

      &:not(:last-child) {
        margin-right: 6px;
      }
    }

    .menu-link {
      padding: 0 10px;
    }

    .menu-item:hover > .menu-link {
      background: 0;
    }
  }
}

.acc-menu {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  &__link {
    display: block;
    font-size: 12px;
    line-height: 20px;
    padding: 10px 20px;
    color: #333;

    &:hover {
      color: #333;
      background-color: #ececec;
      text-decoration: none;
    }

    &.active {
      background-color: #ececec;
    }

    .fa {
      margin-right: 30px;
      color: #9b9b9b;
    }
  }
}
