.logo {
  display: inline-flex;
  margin: 14px 0;

  img {
    height: 42px;
  }
}

.header {
  position: relative;
  z-index: 100;
  background-color: $white;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__elements {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__btn {
    display: flex;
    flex-direction: column;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    position: relative;

    &:hover {
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 16px;
    }

    span {
      display: none;
    }

    img {
      height: 21px;
    }
  }

  &__btn-number {
    display: flex;
    width: 16px;
    height: 16px;
    justify-content: center;
    align-items: center;
    color: $white;
    font-size: 11px;
    line-height: 16px;
    background-color: $danger;
    border-radius: 50%;
    position: absolute;
    top: -3px;
    left: 50%;
  }

  @media (min-width: 1200px) {
    &__btn {
      width: auto;
      height: auto;

      span {
        display: block;
        font-size: 12px;
        text-transform: uppercase;
        color: $primary;
        white-space: nowrap;
        margin-top: 4px;
      }

      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
}

.navigation {
  padding: 0 0 8px;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  @media (min-width: 1200px) {
    padding: 0;
  }
}

.lang {
  position: relative;
  z-index: 1;

  &__toggle {
    display: inline-flex;
    height: 35px;
    align-items: center;
    background-color: $white;
    color: #333;
    padding: 0 5px;
    border-radius: 4px;

    &:hover {
      color: #333;
      text-decoration: none;
    }

    img {
      height: 22px;
    }
  }

  &__dropdown {
    display: none;
    min-width: 160px;
    border-radius: 4px;
    padding: 4px 0;
    position: absolute;
    z-index: 10;
    top: 100%;
    right: -15px;
    margin-top: 8px;
    background-color: $white;
    @include shadow("xs");

    &::before,
    &::after {
      content: "";
      border: 5px solid transparent;
      border-bottom-color: #999;
      position: absolute;
      bottom: 100%;
      right: 40px;
    }

    &::after {
      margin-top: 1px;
      border-bottom-color: $white;
    }
  }

  &__item {
    display: flex;
    height: 40px;
    align-items: center;
    padding: 4px 20px;
    color: #333;

    &:hover {
      color: #333;
      text-decoration: none;
      background-color: $light;
    }

    img {
      height: 16px;
      margin-right: 16px;
    }

    &:not(:last-child) {
      border-bottom: 1px dashed $border-color;
    }
  }
}

.search {
  flex-grow: 1;
  position: relative;

  button {
    background: 0;
    border: 0;
    color: #757575;
    font-size: 16px;
    padding: 0 16px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-55%);
  }

  @media (min-width: 1200px) {
    position: absolute;
    top: -35px;
    left: 180px;
    right: 490px;
    transform: translateY(-50%);

    .form-control {
      height: 42px;
      border: 2px solid $primary;
      padding-left: 14px;
    }
  }
}

.h-dropdown {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;

  &:not(:last-child) {
    margin-right: 24px;
  }

  &__toggle {
    display: flex;
    flex-direction: column;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    position: relative;

    &:hover {
      text-decoration: none;
    }

    span {
      display: none;
    }

    img {
      height: 21px;
    }
  }

  &__menu {
    display: none;
    width: 300px;
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 100;
    background-color: $white;
    border-radius: 4px;
    @include shadow("");

    &::before,
    &::after {
      content: "";
      border: 8px solid transparent;
      border-bottom-color: #ccc;
      position: absolute;
      bottom: 100%;
      right: 114px;
      transform: translateX(50%);
    }

    &::after {
      margin-top: 1px;
      border-bottom-color: $white;
    }

    &--sm {
      width: 180px;
    }

    &--user {
      &::before,
      &::after {
        right: 16px;
      }
    }
  }

  &__menu--user &__item {
    white-space: nowrap;
  }

  &__item {
    display: block;
    padding: 8px 20px;
    font-size: 13px;
    color: #333;
    margin: 4px 0;

    & + & {
      margin-top: -4px;
    }

    &:hover {
      color: $secondary;
      background-color: $light;
      text-decoration: none;
    }
  }

  &__viewall {
    display: block;
    text-align: center;
    border-top: 1px solid $border-color;
    font-size: 13px;
    padding: 10px;
    color: $secondary;

    &:hover {
      color: $secondary;
      text-decoration: none;
    }
  }

  @media (min-width: 1200px) {
    &:not(:last-child) {
      margin-right: 35px;
    }

    &__toggle {
      width: auto;
      height: auto;

      span {
        display: block;
        font-size: 12px;
        text-transform: uppercase;
        color: $primary;
        white-space: nowrap;
        margin-top: 4px;
      }
    }

    &__menu {
      &::before,
      &::after {
        right: 130px;
      }

      &--user {
        &::before,
        &::after {
          right: 16px;
        }
      }
    }
  }
}

.h-avatar {
  width: 30px !important;
  height: 30px !important;
  object-fit: cover;
  object-position: 50% 50%;
  border-radius: 50%;
}

.h-notification {
  padding: 10px;
  color: #444;

  &:hover {
    color: #444;
    text-decoration: none;
    background-color: rgba($secondary, 0.3);
  }

  & + & {
    border-top: 1px solid $border-color;
  }

  &:first-child {
    border-radius: 4px 4px 0 0;
    overflow: hidden;
  }

  &__icon {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    object-fit: contain;
    object-position: 50% 50%;
    margin-right: 10px;
  }

  &__text {
    @include max-line(2);
    font-size: 12px;
  }

  &__time {
    font-size: 10px;
    color: #999;
    margin-top: 4px;
  }
}

.h-btns {
  display: block;
  border-bottom: 1px solid $border-color;
  padding: 6px 20px 16px;
  margin-bottom: 10px;

  &__btn {
    display: block;
    background-color: #e1e1e1;
    transition: 0.3s;
    padding: 10px 16px;
    border-radius: 4px;
    font-size: 14px;
    line-height: 20px;
    color: #000;

    &:hover {
      color: #000;
      background-color: #ccc;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &--primary {
      color: $white;
      background-color: $primary;

      &:hover {
        color: $white;
        background-color: darken($primary, 8%);
      }
    }
  }

  @media (min-width: 1200px) {
    display: flex;
    margin: 0 35px;
    padding: 0;
    border: 0;

    &__btn {
      &:not(:last-child) {
        margin-bottom: 0;
        margin-right: 16px;
      }
    }
  }
}
