.sticky-btns {
  position: fixed;
  bottom: 20px;
  left: 20px;
  z-index: 50;
  display: flex;
  flex-direction: column;

  &__item {
    display: flex;
    width: 36px;
    height: 36px;
    justify-content: center;
    align-items: center;
    background-color: $white;
    border: 3px solid $white;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(#000, 0.3);
    transition: 0.3s;

    &:hover {
      box-shadow: 0 0 15px rgba(#000, 0.5);
      text-decoration: none;
      transform: scale(1.03);
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    img {
      max-height: 100%;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }

  @media (max-width: 767px) {
    bottom: 15px;
    right: 15px;

    &__item {
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }
}
