.md-success {
  &__content {
    background: url("../images/modal-bg.jpg") center/cover no-repeat;
    border-radius: 20px;
    padding: 50px 30px;
    font-size: 14px;
    line-height: 24px;
  }

  @media (min-width: 576px) {
    .modal-dialog {
      max-width: 600px;
    }
  }
}

.md-cart {
  &__content {
    border-radius: 20px;
    padding: 30px;
  }

  &__btns {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;

    .button {
      text-transform: uppercase;
      min-width: 205px;

      & + .button {
        margin-left: 30px;
      }
    }
  }

  @media (min-width: 576px) {
    .modal-dialog {
      max-width: 600px;
    }
  }

  @media (max-width: 575px) {
    &__content {
      padding: 30px 20px;
    }

    &__btns {
      flex-direction: column;

      .button + .button {
        margin-left: 0;
        margin-top: 10px;
      }
    }
  }
}

.md-sign {
  &__content {
    display: grid;
    grid-template-columns: 350px 1fr;
  }

  &__form {
    display: flex;
    flex-direction: column;
    background-color: $light;
    padding: 15px;
    border-radius: 0 4px 4px 0;
  }

  &__nav {
    display: flex;
    flex-wrap: wrap;
    background-color: $white;
    border-bottom: 1px solid $border-color;
    padding: 0 30px;
    border-radius: 4px 4px 0 0;
  }

  &__nav-item {
    display: block;
    font-size: 16px;
    line-height: 45px;
    padding: 0 36px;
    color: #333;

    &:hover {
      color: #333;
      text-decoration: none;
    }

    &.active {
      color: $secondary;
      box-shadow: inset 0 -3px 0 $secondary;

      &:hover {
        color: $secondary;
        text-decoration: none;
      }
    }
  }

  &__body {
    flex-grow: 1;
    background-color: $white;
    padding: 20px;
    border-radius: 0 0 4px 4px;
  }

  &__info {
    padding: 50px 30px;
  }

  &__title {
    font-size: 30px;
    margin-bottom: 16px;
  }

  &__desc {
    font-size: 16px;
    margin-bottom: 24px;
  }

  @media (min-width: 1200px) {
    .modal-dialog {
      max-width: 920px;
    }
  }

  @media (max-width: 1199px) {
    &__content {
      grid-template-columns: 1fr;
    }

    &__info {
      display: none;
    }

    &__form {
      padding: 0;
      border-radius: 4px;
    }
  }

  @media (max-width: 575px) {
    &__nav-item {
      padding: 0 10px;
      flex: 1;
      text-align: center;
    }
  }
}
