.news {
  &__frame {
    @include frame(
      "rect",
      66.66%
    );
    @include frame(
      "zoom"
    );
    width: 270px;
    margin-right: 16px;
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 8px;
  }

  &__info {
    color: #666;
    margin-bottom: 8px;
  }

  &__desc {
    @include max-line(
      5
    );
  }

  @media (max-width: 1199px) {
    &__frame {
      width: 220px;
    }

    &__desc {
      @include max-line(
        3
      );
    }
  }

  @media (max-width: 575px) {
    &__frame {
      width: 50%;
    }

    &__info {
      margin-bottom: 0;
    }

    &__title {
      font-size: 13px;
    }

    &__desc {
      display: none;
    }
  }
}

.news-2 {
  &__frame {
    @include frame(
      "rect",
      66.66%
    );
    @include frame(
      "zoom"
    );
    width: 280px;
    margin-right: 30px;
  }

  &__title {
    font-size: 24px;
    font-weight: 400;
    line-height: 1.25;
    margin-bottom: 10px;
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__label {
    display: inline-block;
    font-size: 14px;
    line-height: 24px;
    padding: 0
      15px;
    background-color: $primary;
    color: $white;
    margin-right: 20px;

    &:hover {
      color: $white;
      text-decoration: none;
    }
  }

  &__comments {
    display: inline-block;
    margin-left: auto;
    font-size: 12px;
    line-height: 20px;
    width: 20px;
    text-align: center;
    color: $white;
    background-color: $primary;
    position: relative;

    &::after {
      content: "";
      border: 3px
        solid
        transparent;
      border-color: $primary
        transparent
        transparent
        $primary;
      position: absolute;
      top: 100%;
      left: 0;
    }
  }

  &__desc {
    @include max-line(
      2
    );
    margin-top: 15px;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 16px;
    }

    &__info {
      &::before {
        content: "";
        display: block;
        flex: 0
          0
          100%;
      }
    }

    &__label {
      order: -1;
      margin-bottom: 8px;
    }
  }

  @media (max-width: 767px) {
    &__frame {
      width: 220px;
      margin-right: 10px;
    }

    &__info {
      margin-bottom: 0;
    }

    &__desc {
      display: none;
    }
  }

  @media (max-width: 575px) {
    flex-wrap: wrap;

    &__frame {
      width: 100%;
      margin-right: 0;
      margin-bottom: 15px;
    }

    &__title {
      font-size: 14px;
    }
  }
}

.news-3 {
  &__frame {
    @include frame(
      "square"
    );
    @include frame(
      "zoom"
    );
    width: 60px;
    margin-right: 10px;
  }

  &__title {
    @include max-line(
      3
    );
    font-size: 14px;
    margin-bottom: 0;
  }
}

.news-4 {
  &__frame {
    @include frame(
      "rect",
      60%
    );
    @include frame(
      "zoom"
    );
  }

  &__label {
    display: inline-block;
    font-size: 12px;
    line-height: 24px;
    padding: 0
      15px;
    background-color: $primary;
    color: $white;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;

    &:hover {
      color: $white;
      text-decoration: none;
    }
  }

  &__title {
    font-size: 14px;
    margin: 16px
      0
      0;
  }

  @media (max-width: 575px) {
    &__title {
      font-size: 13px;
      margin-top: 10px;
    }
  }
}

.product {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: $white;
  padding: 10px;
  position: relative;

  &__frame {
    @include frame(
      "square"
    );
    margin-bottom: 10px;
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    position: relative;
    margin-bottom: 10px;
  }

  &__title-2 {
    font-size: 16px;
    font-weight: 700;
    position: relative;
    padding-bottom: 10px;
    text-align: center;
    margin-bottom: 10px;

    &::after {
      content: "";
      display: block;
      width: 30px;
      border-bottom: 1px
        solid
        #333;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(
        -50%
      );
    }

    a {
      @include max-line(
        1
      );
    }
  }

  &__desc {
    @include max-line(
      3
    );
    text-align: center;
    margin-bottom: 10px;
  }

  &__price {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 16px;
    font-weight: 700;
    color: $danger;
    margin-top: auto;

    del {
      font-size: 0.875em;
      font-weight: 400;
      margin-left: 1em;
      color: #333333;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }

  &__state {
    font-size: 12px;
    line-height: 18px;
    border-radius: 9px;
    color: $white;
    background-color: $secondary;
    text-align: center;
    margin-top: 10px;
    position: relative;
    z-index: 1;
  }

  &__progress {
    position: absolute;
    display: block;
    border-radius: 9px;
    background-color: rgba(
      $primary,
      0.5
    );
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    height: 100%;
  }

  &__discount {
    display: flex;
    width: 45px;
    height: 45px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: $danger;
    color: $white;
    font-size: 14px;
    font-weight: 700;
    color: $white;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 10;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 13px;
    }

    &__title-2 {
      font-size: 13px;
    }

    &__desc {
      font-size: 12px;
    }

    &__price {
      font-size: 14px;
    }

    &__discount {
      width: 40px;
      height: 40px;
    }
  }

  @media (max-width: 575px) {
    &__price {
      font-size: 13px;
    }

    &__info {
      font-size: 12px;
    }
  }
}

.product-2 {
  max-height: 100%;
  overflow: hidden;

  &
    + & {
    margin-top: 15px;
  }

  &__title {
    @include max-line(
      4
    );
    font-size: 14px;
    margin-bottom: 0;
  }

  &__rank {
    display: flex;
    flex-direction: column;
    align-self: stretch;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #333;
  }

  &__frame {
    @include frame(
      "square"
    );
    width: 100px;
    margin-left: 15px;
    margin-right: 15px;
  }
}

.product-4 {
  &__frame {
    @include frame(
      "square"
    );
    width: 240px;
    margin-right: 30px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__price {
    font-size: 18px;
    color: $danger;
    font-weight: 700;
    line-height: 30px;
    margin-bottom: 16px;

    del {
      font-size: 14px;
      font-weight: 400;
      color: #333;
    }
  }

  &__discount {
    display: inline-block;
    font-size: 14px;
    line-height: 30px;
    padding: 0
      10px;
    border-radius: 4px;
    background-color: $secondary;
    margin-left: 15px;
    color: $white;
  }

  &__info {
    margin-bottom: 10px;
  }

  &__desc {
    margin-bottom: 16px;
  }

  @media (max-width: 1199px) {
    flex-wrap: wrap;

    &__frame {
      width: 200px;
      margin-bottom: 20px;
    }

    &__body {
      flex: 0
        0
        100%;
    }
  }
}

.product-6 {
  &__frame {
    @include frame(
      "square"
    );
    width: 60px;
    border: 1px
      solid
      $border-color;
    margin-right: 10px;
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    position: relative;
    margin-bottom: 4px;
  }

  &__price {
    font-size: 16px;
    font-weight: 700;
    color: $danger;
    margin-top: auto;
    margin-bottom: 4px;

    del {
      font-size: 14px;
      font-weight: 400;
      color: #333333;
      margin-left: 20px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
  }

  &__link {
    font-size: 14px;
    font-weight: 700;
    color: $secondary;
    text-decoration: underline;

    &:hover {
      color: $secondary;
    }
  }

  @media (max-width: 575px) {
    &__title {
      font-size: 13px;
    }

    &__price {
      font-size: 14px;

      del {
        font-size: 12px;
        margin-left: 12px;
      }
    }

    &__info {
      font-size: 12px;
    }
  }
}

.product-9 {
  position: relative;
  padding-right: 270px;

  &__frame {
    @include frame(
      "square"
    );
    width: 127px;
    margin-right: 20px;
  }

  &__title {
    font-size: 14px;
    margin-bottom: 10px;

    a {
      color: #333;

      &:hover {
        color: $secondary;
        text-decoration: none;
      }
    }
  }

  &__info {
    margin-bottom: 15px;

    a,
    a:hover {
      color: $secondary;
      text-decoration: none;
    }
  }

  &__btns {
    display: flex;
    flex-wrap: wrap;
  }

  &__btn {
    font-size: 14px;
    color: $secondary;

    &:hover {
      color: $secondary;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__quantity {
    position: absolute;
    top: 0;
    right: 0;
  }

  &__price {
    position: absolute;
    top: 0;
    left: calc(
      100% -
        260px
    );

    del {
      color: #999;
    }
  }

  &--custom {
    padding-right: 0;
    max-width: 420px;
    margin: 0
      auto;
  }

  &--custom
    + &--custom {
    margin-top: 20px;
  }

  @media (max-width: 767px) {
    padding-right: 0;

    &__info {
      margin-bottom: 10px;
    }

    &__quantity {
      position: static;
      margin-bottom: 10px;
    }

    &__price {
      position: static;
      margin-bottom: 10px;
    }
  }

  @media (max-width: 575px) {
    &__frame {
      width: 100px;
      margin-right: 10px;
    }

    &__info {
      font-size: 12px;
    }
  }
}

.product-10 {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
  transition: 0.4s;
  background-color: $white;
  position: relative;
  z-index: 1;

  &:hover {
    @include shadow(
      ""
    );
    z-index: 2;
  }

  &__frame {
    @include frame(
      "square"
    );
    margin-bottom: 10px;
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 16px;
  }

  &__btn {
    display: block;
    text-align: center;
    background-color: $secondary;
    text-align: center;
    font-size: 14px;
    font-weight: 700;
    line-height: 30px;
    margin-top: auto;
    color: $white;
    border-radius: 4px;

    &:hover {
      color: $white;
      text-decoration: none;
      background-color: darken(
        $secondary,
        10%
      );
    }
  }

  @media (max-width: 1199px) {
    padding: 10px;

    &__title {
      font-size: 13px;
      margin-bottom: 10px;
    }

    &__btn {
      font-size: 13px;
    }
  }
}

.product-11 {
  &__frame {
    @include frame(
      "square"
    );
    width: 80px;
    margin-right: 10px;
  }

  &__title {
    font-size: 14px;
    margin-bottom: 8px;
  }

  &__info {
    font-size: 14px;
    line-height: 24px;
  }
}

.calendar {
  background-color: $white;
  padding: 15px;
  border-radius: 4px;
  color: #333;

  &:hover {
    color: #333;
    text-decoration: none;
  }

  &__time {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 56px;
    text-align: center;
    border-radius: 4px;
    background-color: $secondary;
    margin-right: 10px;
    color: $white;
    padding: 4px;
    line-height: 24px;
  }

  &__date {
    font-size: 16px;
    font-weight: 700;
  }

  &__title {
    font-size: 14px;
    line-height: 20px;
    color: #333;
  }

  &__price {
    font-size: 16px;
    font-weight: 700;
    color: $danger;
    line-height: 20px;

    del {
      font-size: 14px;
      font-weight: 400;
      margin-left: 10px;
      color: #333;
    }
  }
}

.expert {
  display: block;
  background-color: $white;
  box-shadow: 0 1px 8px rgba(#000, 0.08);
  padding: 24px 20px;
  color: inherit;
  transition: 0.3s;

  &:hover {
    color: inherit;
    text-decoration: none;
    box-shadow: 0 3px 8px rgba(#000, 0.16);
  }

  &__avatar {
    width: 120px;
    max-width: 50%;
    position: relative;
    border-radius: 50%;
    border: 2px solid $white;
    box-shadow: 0 1px 8px rgba(#000, 0.08);
    margin: 0 auto 24px;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-bottom: 100%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &__name {
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 8px;
  }

  &__desc {
    @include max-line(4);
    text-align: center;
  }

  @media (max-width: 575px) {
    padding: 20px 16px;

    &__avatar {
      margin-bottom: 16px;
    }

    &__name {
      font-size: 14px;
      margin-bottom: 6px;
    }

    &__desc {
      font-size: 12px;
    }
  }
}

.course {
  background-color: $white;
  transition: 0.4s ease-out;
  box-shadow: 0 1px 10px rgba(#000, 0.1);

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 15px rgba(#000, 0.3);
  }

  &__frame {
    @include frame('rect', 56.25%);
  }

  &__body {
    padding: 16px 20px;
  }

  &__title {
    @include max-line(2);
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 12px;
  }

  &__author {
    font-weight: 300;
  }

  &__rating {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #767676;
  }

  &__old-price {
    text-align: right;
    text-decoration: line-through;
    font-size: 12px;
    font-weight: 300;
  }

  &__price {
    text-align: right;
    font-size: 16px;
    font-weight: 700;
    color: $danger;
    line-height: 1.2;
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 14px;
    }

    &__author {
      font-size: 12px;
    }

    &__old-price {
      font-size: 12px;
    }

    &__price {
      font-size: 14px;
    }
  }

  @media (max-width: 575px) {
    &__body {
      padding: 10px;
    }

    &__author {
      font-size: 11px;
    }

    &__rating {
      font-size: 11px;
    }

    &__price-col {
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: space-between;
      margin-top: 6px;
    }

    &__price {
      margin-left: auto;
    }
  }
}

.course-2 {
  padding: 24px;
  border-radius: 4px;
  background-color: $white;

  &__frame {
    @include frame('rect', 50%);
    width: 400px;
  }

  &__title {
    font-size: 1.43em;
    font-weight: 700;
    margin-bottom: 0.5em;

    a {
      color: inherit;

      &:hover {
        color: $primary;
        text-decoration: none;
      }
    }
  }

  &__info {
    color: #999;
    margin-bottom: 8px;
  }

  &__intro {
    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        padding-left: 24px;
        position: relative;

        &:not(:last-child) {
          margin-bottom: 4px;
        }

        &::before {
          content: '\f105';
          font-family: 'fontawesome';
          font-weight: 700;
          color: $secondary;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }

  &__price {
    font-size: 1.75em;
    font-weight: 700;
    color: $danger;
    line-height: 1.2;
  }

  &__old-price {
    font-size: 1.25em;
    text-decoration: line-through;
  }

  &__off {
    font-size: 1em;
    font-weight: 700;
  }

  &__btn {
    text-transform: uppercase;
    font-size: 1em;
    padding-left: 30px;
    padding-right: 30px;
    margin-top: 12px;
  }

  @media (max-width: 1199px) {
    font-size: 14px;

    &__frame {
      width: 300px;
    }

    &__info {
      font-size: 12px;
    }

    &__intro {
      font-size: 12px;

      ul {
        li {
          padding-left: 16px;
        }
      }
    }
  }

  @media (max-width: 991px) {
    &__frame {
      width: 100%;
    }
  }
}
