.list {
  &__item {
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid $border-color;
  }
}

.list-2 {
  &__item {
    &:not(:last-child) {
      border-bottom: 1px dashed $border-color;
      padding-bottom: 15px;
      margin-bottom: 15px;
    }
  }
}

.product-2-list {
  margin: 0 -10px -10px;

  .product-2 {
    margin: 0;
    padding: 10px;

    &:hover {
      @include shadow("");
    }
  }
}
